import Vue from 'vue'
import VueRouter from 'vue-router'

import toyApp from '../pages/toy-app.vue'
import toyDetails from '../pages/toy-details.vue'
import toyEdit from '../pages/toy-edit.vue'
import toyAppHome from '../pages/home'
import toyChart from '../pages/toy-chart.vue'
import toyAbout from '../pages/toy-about.vue'
Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'home',
    component: toyAppHome
  },
  {
    path: '/toy',
    name: 'toy-app',
    component: toyApp
  },
  {
    path: '/toy/details/:id',
    name: 'toy-details',
    component: toyDetails
  },
  {
    path: '/toy/edit/:id?',
    name: 'toy-edit',
    component: toyEdit
  },
  {
    path: '/toy/chart',
    name: 'toy-chart',
    component: toyChart
  },
  {
    path: '/toy/about',
    name: 'toy-about',
    component: toyAbout
  }
]

const router = new VueRouter({
  routes
})

export default router
