<template>

  <section class="gender-picker flex column">
    <h2>Choose Your Gender!</h2>
    <label for="gender-picker">Choose Your Gender!</label>
    <select name="gender-picker" @change="emitGender" v-model="gender" id="gender-picker">
    <option value="Boy">Boy</option>
    <option value="Girl">Girl</option>
    <option value="Unicorn">Unicorn</option>
    </select>
  </section>

</template>

<script>
export default {
  data(){
    return{
      gender: null,
    }
  },
  emitGender(){
    this.$emit('setGender', this.gender)
  }
};



</script>

