<template>
  <section class="toy-edit">
    <h2>Edit Page</h2>
    <el-form @submit="saveToy" v-if="toyToEdit" ref="ruleForm">
      <el-form-item v-model="toyToEdit.name">
        <el-input type="text" v-model="toyToEdit.name" placeholder="Toy's Name"></el-input>
      </el-form-item>

      <el-form-item>
        <el-input
          type="number"
          placeholder="Toy's Price"
          v-model.number="toyToEdit.price"
          clearable
        ></el-input>
      </el-form-item>

      <el-form-item>
        <el-form-item label="In Stock">
          <el-switch v-model="toyToEdit.inStock"></el-switch>
        </el-form-item>
      </el-form-item>
      <el-form-item>
        <el-select v-model="toyToEdit.type" placeholder="Toy's Type">
          <el-option label="Educational" value="Educational"></el-option>
          <el-option label="Funny" value="Funny"></el-option>
          <el-option label="Adult" value="Adult"></el-option>
        </el-select>
      </el-form-item>


     <img v-if="isUploading" src="@/assets/img/spinner.gif" alt="">
    
      <el-form-item>
        <el-button type="primary" @click="saveToy" :disabled='isUploading'>Create</el-button>
      </el-form-item>
    </el-form>

    <input type="file" @change="onUploadImg" />


  </section>
</template>

<script>
import { toyService } from "../services/toy.service";
import { uploadImg } from "../services/imgUpload.service.js";

export default {
  data() {
    return {
      isUploading:false,
      labelPosition: "",
      
      toyToEdit: {
        name: "",
        price: "",
        type: "",
        inStock: "",
        img: ""
      }
    };
  },
  methods: {
    saveToy() {
      console.log("in save", this.toyToEdit);
      this.$store
        .dispatch({ type: "saveToy", toy: this.toyToEdit })
        .then(() => {});
    },
    onUploadImg(ev) {
      this.isUploading= true;
      uploadImg(ev)
      .then(res => {
       this.toyToEdit.img = res.url
       this.isUploading= false;
      });
    },
  },
  created() {
    const toyId = this.$route.params.id;
    if (toyId) {
      toyService.getById(toyId).then(toy => {
        this.toyToEdit = JSON.parse(JSON.stringify(toy));
      });
    }
  }
};
</script>

