<template>
  <div class="flex column chat-with-cs">
    <button @click="showChat=!showChat" class="chat-button">Chat</button>
    
    <div  class="conversation-container" v-if="showChat">
      <div v-for="(line, index) in textLines" :key="index">{{line}}</div>
      <input type="text" name="chat-input" @keyup.enter="addLine" v-model="text" />
      </div>
   
  </div>

</template>
<script>
export default {
  data() {
    return {
      text: "",
      textLines: [],
      showChat: false
    };
  },
  methods: {
    addLine() {
      this.textLines.push("You: " + this.text);
      this.textLines.push("Support: Sure thing honey, one sec.");
      this.text = "";
    }
  }
};
</script>


