<template>
  <section class="toy-details">
    <section v-if="toyToShow">


      <img :src="getUrl" />
      <h2>{{toyToShow.name | capitalize }}</h2>
      <p>Price: {{toyToShow.price | formatNum}}</p>
      <p>Type: {{toyToShow.type}}</p>
      <p>Type: {{toyToShow.createdAt | formatTime | capitalize}}</p>

      <p>Id: {{toyToShow._id}}</p>
      <p>Availability: {{isAvailable}}</p>
      <button @click="editToy">Edit Toy</button>
    </section>
  </section>
</template>


<script>
import { toyService } from "../services/toy.service.js";

export default {
  name: "toy-details",

  data() {
    return {
      toyToShow: null
    };
  },

  methods: {
    loadToy() {
      let id = this.$route.params.id;
      toyService.getById(id).then(toy => {
        toy;
        this.toyToShow = JSON.parse(JSON.stringify(toy));
      });
    },
    editToy() {
      this.$router.push("/toy/edit/" + this.toyToShow._id);
    }
  },

  computed: {
    isAvailable() {
      return this.toyToShow.inStock ? "In Stock" : "Not In Stock";
    },
    getUrl() {
      let url = "https://robohash.org/" + this.toyToShow.name + ".png";
      console.log(url);
      return url;
    }
  },

  components: {},
  created() {
    this.loadToy();
  }
};
</script>



