<template>
  <el-row  class="main-card-container" >
    <!-- <el-col :span="4" > -->
      <el-card :body-style="{ padding: '0px' }">
        <div style="padding: 14px;">
    
      <!-- <img :src="getUrl"> -->
      <img :src="toy.img">
      <h2>{{toy.name | capitalize }}</h2>
      <p>Price:   {{toy.price | formatNum}}</p>
      <p>Type: {{toy.type}}</p>
      <p>Type:  {{toy.createdAt | formatTime | capitalize}}</p>

      <p>Id: {{toy._id}}</p>
      <p>Availability: {{isAvailable}}</p>

          <div class="bottom clearfix">
            <el-button type="text" class="button" @click="deleteToy(toy._id)">Delete</el-button>
            <el-button type="text" class="button-left" @click="goToDetails">Details</el-button>
          </div>
        </div>
      </el-card>
    <!-- </el-col> -->
  </el-row>
</template>


<script>

export default {
     props: {
       toy: {
        type: Object,
        required: true
     },
     },
  data() {
    return {
      currentDate: new Date()
    };
  },
  computed: {
    isAvailable() {
      return this.toy.inStock ? "In Stock" : "Not In Stock";
    },
    getUrl(){
      let url = "https://robohash.org/"+this.toy.name+".png"
      console.log(url)
      return url
    }
  },
  methods:{
      goToDetails(){
          this.$router.push('toy/details/'+ this.toy._id)
      },
        deleteToy(toyId) {
      this.$emit("deleteToy", toyId);
    }
  },
  created(){
    console.log(this.toy)
  }
}
</script>


