import Vue from 'vue'
import Vuex from 'vuex'

import { toyService } from '../services/toy.service.js';

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    toys: [],
    filterBy: {
      sort: '',
      name: '',
      inStock: '',
      type: '',
    }
  },
  getters: {
    toys(state) {
      return state.toys
    }
  },
  mutations: {
    setToys(state, { toys }) {
      state.toys = toys
    },
    setFilter(state, { filterBy }) {
      state.filterBy = filterBy
    },
    removeToy(state, { id }) {
      const idx = state.toys.findIndex(toy => toy._id === id)
      state.toys.splice(idx, 1);
    },
    updateToy(state ,{toy}){
      const idx = state.toys.findIndex(toy => toy._id === id)
      state.toys.splice(idx, 1,toy);
    },
    addToy(state, {toy}){
      state.toys.unshift(toy);
    }

  },
  actions: {
    loadToys({ commit, state }) {
      return toyService.query(state.filterBy) 
      .then(toys => {
      commit({ type: 'setToys', toys })
          return toys
        })
    },
    removeToy({ commit, state }, { id }) {
      const keepToy = state.toys.find(toy=> toy._id === id) //OPTIMISTIC MUTATION DOESNT WORK, ADDS TOY WHEN SERVER IS DOWN.
      return toyService.remove(id)
        .then(() => {
          commit({ type: 'removeToy', id })
        })
        .catch((err) =>{
          // commit({type: 'addToy',toy: keepToy}) 
         throw err;
        })
    },
    saveToy({ commit }, { toy }) {
      console.log('is save toy STORE', toy);
      const type = (toy._id) ? 'updateToy' : 'addToy'
      return toyService.save(toy)
          .then((savedToy) => {
              console.log('saved: ', savedToy)
              commit({ type, toy: savedToy })
          })
    },
  },
  modules: {
  }
})
