 
<template>
  <div class="toy-app">
    <h1>Toys R Us ?</h1>
  
   <router-link to="toy/edit" class="add-link"> Add A New Toy!  </router-link> 
    <toy-filter @setFilter="setFilter"></toy-filter>
    <p>Displaying {{toys.length}}  Toys To Choose From!</p>
    <toy-list @deleteToy="deleteToy" :toys="toys"></toy-list>
  </div>
</template>

<script>

import { toyService } from "../services/toy.service.js";
import toyList from "../cmps/toy-list.vue";
import toyFilter from "../cmps/toy-filter.vue";

export default {
  data() {
    return {
      
    };
  },
  computed: {
    toys() {
      return this.$store.getters.toys;
    }
  },
  created() {
    const toys = this.$store.dispatch({ type: "loadToys" });
    console.log("toys", toys);
  },
  components: {
    toyList,
    toyFilter
  },
  methods: {  
    setFilter(filterBy) {
      this.$store.commit({ type: "setFilter", filterBy: {...filterBy } });
      this.$store.dispatch({ type: "loadToys" });
    },
    deleteToy(toyId) {
      console.log(toyId)
      this.$store.dispatch({ type: "removeToy", id: toyId });
    }
  }
};
</script>


