<template>
  <section class="toy-preview">
    <div>
      <pre>Name: {{toy.name | capitalize }}</pre>
      <pre>Price: {{toy.price | formatNum}}</pre>
      <pre>Type: {{toy.type}}</pre>
      <pre>Type: {{toy.createdAt | formatTime | capitalize}}</pre>

      <pre>Id: {{toy._id}}</pre>
      <pre>Availability: {{isAvailable}}</pre>
      <router-link :to="'toy/details/'+ toy._id">Details</router-link>
      <button @click="deleteToy(toy._id)">X</button>
    </div>
  </section>
</template>

<script>
export default {
  props: {
    toy: {
      type: Object,
      required: true
    }
  },
  data() {
    return {};
  },
  computed: {
    isAvailable() {
      return this.toy.inStock ? "In Stock" : "Not In Stock";
    }
  },
  methods: {
    deleteToy(toyId) {
      this.$emit("deleteToy", toyId);
    }
  }
};
</script>



<style scoped src="@/assets/styles/cmps/preview.css" lang="">
</style>