import axios from 'axios'
const KEY = "toyService";
export const toyService = {
    query,
    getById,
    remove,
    save,
}


const BASE_URL = (process.env.NODE_ENV !== 'development') ? '/api/toy' : '//localhost:3030/api/toy';
function query(filterBy) {
    console.log('in query');
    console.log('filterby',filterBy);
    const inStock = (filterBy.inStock === '') ? '' : `inStock=${filterBy.inStock}`
    const type = (!filterBy.type) ? '' : `type=${filterBy.type}`
    const sort = (!filterBy.sort) ? '' : `_sort=name&_order=${filterBy.sort}`
    return axios.get(`${BASE_URL}?name=${filterBy.name}&${inStock}&${type}&${sort}`)
        .then(res => res.data)
}

function getById(id) {
    return axios.get(`${BASE_URL}/${id}`)
        .then(res => res.data)
}

function remove(id) {
    return axios.delete(`${BASE_URL}/${id}`)
}

function save(toy) {

    return (toy._id) ? _update(toy) : _add(toy)
}

